* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 100vw;
  height: 100vh;
  text-align: center;
  background-color: #1e9999;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

header {
  z-index: 99;
  justify-content: center;
  align-items: center;
  padding: .5rem;
  display: flex;
  position: sticky;
  top: 0;
}

header img {
  height: 2rem;
  padding-left: 1rem;
}

nav {
  width: 100vw;
  align-items: center;
  display: flex;
}

.nav-bar {
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.dropdown-li {
  padding-right: 4rem;
  display: none;
  position: relative;
}

.cart {
  padding-right: 1rem;
}

nav i {
  font-size: x-large;
}

nav a {
  color: red;
  text-decoration: none;
}

nav a:hover {
  color: #db7093;
}

.search {
  background-color: red;
  display: flex;
}

form input {
  border: none;
}

button {
  color: #fff;
  background-color: red;
  border: none;
  padding: 5px;
}

.dropdown-li:hover .dropdown {
  width: -moz-fit-content;
  width: fit-content;
  text-align: center;
  background-color: #c3c3c3;
  margin-right: 4rem;
  display: block;
  position: absolute;
  right: 0;
}

.dropdown {
  color: #fff;
  display: none;
}

header ul {
  width: 100%;
  justify-content: space-between;
  margin-bottom: 0;
  padding: 0 4rem;
  list-style-type: none;
  display: flex;
}

header .nav-desk a {
  color: red;
  text-shadow: 0 0 10px #f5f3f4;
  padding: 1rem;
  font-size: x-large;
  text-decoration: none;
}

header li:hover a {
  color: #db7093;
}

header p {
  color: #fff;
  top: 0;
  left: 0;
}

ul {
  justify-content: space-around;
  align-items: center;
  padding: 50px;
  display: flex;
}

ul li {
  list-style: none;
}

ul li a {
  color: #f5f5f5;
  padding: 10px;
  font-size: x-large;
  font-weight: bolder;
  text-decoration: none;
}

ul a:hover {
  cursor: pointer;
  color: #7dfab3;
  text-shadow: 0 0 15px;
  border: 3px solid #7dfab3;
  box-shadow: 0 0 15px;
}

main {
  margin: 20px;
}

.icon:nth-child(2):hover {
  border: 5vw solid #7dfab3;
  padding: 4vw;
  transform: scale(.8);
}

main .images {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.icon {
  width: 40vw;
  height: 40vw;
  border-radius: 50%;
  margin: 2rem;
  transition: all 2s;
  animation-name: cross-r;
  animation-duration: 3s;
  box-shadow: 0 0 45px;
}

.icon:nth-child(1) {
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding-bottom: 10%;
  display: flex;
}

.icon:nth-child(2) {
  animation-name: cross-l;
  animation-duration: 3s;
}

.icon img {
  width: 100%;
  height: 100%;
}

@keyframes cross-r {
  0% {
    transform: translateX(100%);
  }

  50% {
    transform: rotate(180deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes cross-l {
  0% {
    transform: translateX(-100%);
  }

  50% {
    transform: rotate(-180deg);
  }

  100% {
    transform: rotate(-360deg);
  }
}

.para {
  color: #fff;
  padding: 0 2vw;
  font-size: 1.5rem;
}

.icon a {
  text-decoration: none;
}

h1 {
  width: -moz-fit-content;
  width: fit-content;
  color: #fff;
  padding: 0 2vw;
  transform: translateY(-10%);
}

.i1, .i2 {
  width: 20vw;
  border-radius: 50%;
}

footer {
  color: #b2b2b2;
  text-align: center;
  position: sticky;
  bottom: 0;
  left: 0;
}

footer ul {
  justify-content: center;
  align-items: center;
  padding: 1rem;
  list-style-type: none;
  display: flex;
}

footer ul a {
  color: red;
  font-size: xx-large;
}

footer a:hover {
  color: #db7093;
}

@media screen and (max-width: 1200px) {
  .dropdown-li {
    display: inline-block;
  }

  nav {
    width: 100vw;
    display: flex;
  }

  .nav-bar {
    justify-content: space-between;
    padding: .5rem 3rem;
    display: flex;
  }

  header ul {
    display: none;
  }

  .para {
    font-size: .5rem;
    display: none;
  }

  .images:hover .para {
    display: block;
  }

  .images:hover h1 {
    display: none;
  }
}

@media screen and (max-width: 480px) {
  header img {
    padding-left: 1vw;
  }

  .dropdown-li {
    padding: 1vw;
    display: inline-block;
  }

  .cart {
    padding-left: 1vw;
  }

  nav {
    width: 100vw;
    display: flex;
  }

  .nav-bar {
    justify-content: flex-end;
    padding: 1vw;
    display: flex;
  }

  .search, header ul {
    display: none;
  }

  .para {
    font-size: .2rem;
    display: none;
  }

  .images:hover .para {
    display: block;
  }

  .images:hover h1 {
    display: none;
  }
}

@media screen and (max-width: 150px) {
  header {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1vw;
    display: flex;
  }
}

